@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';



































































































































.mainpage > div {
  width: 100%;
}
.basket {
  min-height: 65vh;
  font-size: 16px;
  max-width: 1200px;
  margin: 60px auto;
  padding: 0 80px;

  & > div {
    width: 100%;
  }
}
.basket-details {
  display: table;
  table-layout: auto;
  border-spacing: 0 30px;
  width: 100%;

  .table-header {
    display: table-header-group;

    .header-item {
      display: table-cell;
      padding-bottom: 10px;
      border-bottom: 1px solid $line;
    }
  }
  .table-item {
    display: table-row;
    margin: 30px 0;
    width: 100%;

    .product-info {
      display: table-cell;
      vertical-align: middle;
    }
    .product-img {
      width: 100px;
    }
    .product-detail {
      width: 440px;
      max-width: 440px;
      padding-left: 30px;
    }
    .product-price {
      width: 120px;
    }
    .options {
      font-size: 14px;
    }
  }
}
.table-footer {
  display: table;
  table-layout: auto;
  border-spacing: 0 30px;
  width: 100%;
  border-top: 1px solid $line;

  .footer-item {
    display: table-cell;
    vertical-align: middle;
    text-align: right;
    width: 100%;
  }
}
.overflow-ellipsis {
  display: block;
  max-width: inherit;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media (max-width: 600px) {
  .basket {
    padding: 0 20px;
  }
}
